#wnpApp {
  width: 100vw;
  max-width: 100%;
  height: 100vh;
  overflow: hidden;
}

#wnpApp .wnpContainer {
  background-color: #ccc;
  grid-template: "wnpAlbumArt" 1.2fr
                 "wnpMediaInfo" .8fr
                 "wnpFooter" 100px
                 / 1fr;
  grid-auto-flow: row;
  gap: 0;
  width: 100%;
  height: 100%;
  display: grid;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

#wnpApp .wnpAlbumArt {
  text-align: center;
  grid-area: wnpAlbumArt;
  padding: 16px;
  overflow: hidden;
}

#wnpApp .wnpAlbumArt > div {
  background-color: #fff;
  max-width: 100%;
  max-height: 100%;
  overflow: hidden;
  box-shadow: 3px 3px 16px #000000bf;
}

#wnpApp .wnpAlbumArt img {
  width: 100%;
  box-shadow: 3px 3px 16px #000000bf;
}

#wnpApp .wnpMediaInfo {
  grid-area: wnpMediaInfo;
  padding: 0 16px 16px;
}

#wnpApp .wnpMediaInfo > div {
  background-color: #fff;
  height: 100%;
}

#wnpApp .wnpFooter {
  grid-area: wnpFooter;
  padding: 0 16px 16px;
}

#wnpApp .wnpFooter > div {
  background-color: #fff;
  height: 100%;
}

@media (width >= 576px) {
  #wnpApp .wnpContainer {
    grid-template: "wnpAlbumArt wnpMediaInfo" 1fr
                   "wnpFooter wnpFooter" 100px
                   / .9fr 1.1fr;
  }

  #wnpApp .wnpMediaInfo {
    padding: 16px 16px 16px 0;
  }
}

